<template>
  <div class="check-2">
      <el-table :data="list">
          <el-table-column label="" prop="checkName"></el-table-column>
          <el-table-column label="">
            <template scope="scope">
              <el-form v-if="edit">
                <el-form-item label="">
                    <el-radio v-model="scope.row.result" label="1"></el-radio>
                    <el-radio v-model="scope.row.result" label="0"></el-radio>
                </el-form-item>
              </el-form>
              <template v-else>
                {{scope.row.result === '1' ? '' : ''}}
              </template>
            </template>
          </el-table-column>
      </el-table>
  </div>
</template>
<script>
 export default {
   name: 'check-tpl0',
   props: ['list', 'edit', 'admin']
 }
</script>
<style>

</style>



