<template>
	<div>
			<div class="chaxun-top">
				<div>
					<el-form inline>
						<el-form-item label="">
							<el-date-picker v-model="filter.startTime"></el-date-picker> 
								<template slot="prefix">
										<i class="el-icon-search icon-search"></i>
										</template>
										<template slot="suffix">
												<el-button  style="margin-top:5px" type="primary" size="mini"></el-button>
										</template>
							</el-date-picker>
						</el-form-item>

						<el-form-item >
							<el-date-picker v-model="filter.endTime"></el-date-picker> 
								<template slot="prefix">
										<i class="el-icon-search icon-search"></i>
										</template>
										<template slot="suffix">
												<el-button  style="margin-top:5px" type="primary" size="mini"></el-button>
										</template>
							</el-date-picker>
						</el-form-item>

						<el-form-item >
							<el-button type="primary" @click="fetch"></el-button>
						</el-form-item>
					</el-form>
					
        </div>
			</div>
			 
				<el-table :data="list">
          <el-table-column label="id" prop="id"></el-table-column>
          <el-table-column label="" prop="ownerName"></el-table-column>
          <el-table-column label="" prop="ownerPhone"></el-table-column>
          <el-table-column label="" prop="catName"></el-table-column>
          <el-table-column label="" prop="type">
            <template scope="scope">
              <div v-for="item in config.ghxm" :key="item.id" v-if="item.id == scope.row.type">{{item.name}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="createTime"></el-table-column>
					<el-table-column label="" prop="catName"></el-table-column>
					<el-table-column label="">
						<template scope="scope">
							<el-button @click="study(scope.row)"></el-button>
						</template>
					</el-table-column>
				</el-table>
				<!-- <div style="display: flex;justify-content:flex-end;padding-top: 10px">
						<el-pagination
						:page-size="config.page.limit"
						:pager-count="pageinfo.pageNum"
						layout="prev, pager, next"
						
						:total="pageinfo.totalCount">
					</el-pagination>
				</div> -->
				<el-dialog title="" class="chaxun-dialog" :visible="show">
					<div>
						<p style="margin-bottom:18px;">   16 2018/08/09</p>
						<p class="chaxun-dialog-title"></p>
						<div class="chaxun-dialog-content"></div>
						
						<p class="chaxun-dialog-title"></p>
						<div class="chaxun-dialog-content">
							<div class="chaxun-dialog-basic">
								<div>3.6 kg</div>
								<div>3.6 kg</div>
								<div>3.6 kg</div>
							</div>
							
							<div  class="chaxun-dialog-basic">	
								<div>3.6 kg</div>
								<div>3.6 kg</div>
								<div>3.6 kg</div>
							</div>
						</div>

						<p class="chaxun-dialog-title"></p>
						<div class="chaxun-dialog-content">
							<div class="chaxun-dialog-check">
								<div></div>
								<div></div>
							</div>
							
							<div  class="chaxun-dialog-check">	
								<div>3.6 kg</div>
							</div>
						</div>

						<p class="chaxun-dialog-title"></p>
						<div class="chaxun-dialog-content">
							
						</div>
						<p class="chaxun-dialog-title"></p>
						<div class="chaxun-dialog-content">
							<p class="chaxun-dialog-title" style="text-align:center"></p>
							<el-table :data="list">
								<el-table-column label="" prop="name">
										<template scope="scope">
												<router-link to="/treat"></router-link>
										</template>
								</el-table-column>
								<el-table-column label="" prop="status"></el-table-column>
								<el-table-column label="" prop="desc"></el-table-column>
								
        			</el-table>
							<p class="chaxun-dialog-title" style="text-align:center;margin-top:10px">FE/LV</p>
							<el-table :data="list">
								<el-table-column label="" prop="name">
										<template scope="scope">
												<router-link to="/treat"></router-link>
										</template>
								</el-table-column>
								<el-table-column label="" prop="status"></el-table-column>
								<el-table-column label="" prop="desc"></el-table-column>
        			</el-table>
						</div>
					
						<p class="chaxun-dialog-title"></p>
						<div class="chaxun-dialog-content">
							<el-table :data="list">
								<el-table-column label="" prop="name">
										<template scope="scope">
												<router-link to="/treat"></router-link>
										</template>
								</el-table-column>
								<el-table-column label="" prop="status"></el-table-column>
								<el-table-column label="" prop="desc"></el-table-column>
							
        			</el-table>
						</div>
						

						<p class="chaxun-dialog-title"></p>
						<div class="chaxun-dialog-content">
							
						</div>

						
					</div>
					  <span slot="footer" class="dialog-footer">
    					<el-button @click="show = false"> </el-button>
  					</span>
				</el-dialog>
	</div>
</template>
<script>
import corner from '../../../components/corner.vue'
import api from '../api'
export default {
  props: {

  },
  components: {
    corner
  },
  data () {
    return {
 			list: [

      ],
      show: false,
      filter: {
        startTime: '',
        endTime: ''
      },
      pageinfo: {
        totalCount: 0,
        pageNum: 1
      }
    }
  },
  computed: {

  },
  methods: {
    reload () {
      this.fetch()
    },
    study (item) {
      this.$router.push(`/doctor/moni?id=${item.id}&nomoni=1`)
    },
    async fetch () {
      // let filter = {}
      // if (this.filter.startTime) {
      //   filter.startTime = new Date(this.filter.startTime).getTime()
      // }
      // if (this.filter.endTime) {
      //   filter.endTime = new Date(this.filter.endTime).getTime()
      // }
      // let limit = this.config.page.limit
      // let start = this.config.page.limit * (this.pageinfo.pageNum - 1)
      let res = await api.listHistoryLearnCase({ userId: global.user.id })
      let { data: { data, code, pageinfo } } = res
      if (code === 200) {
        this.loading = false
        this.list = data
        this.pageinfo = pageinfo
      }
    }
  },
  created () {

  },
  mounted () {
    this.reload()
  }
}
</script>
<style scoped lang="less">
	.chaxun-top{
		
		margin-bottom: 10px;
	}
</style>


